// src/styles/BakeryMap/SideBarStyles.js
import styled from 'styled-components';

export const Container = styled.div`
  background-color: #FFFDD0;
`;

//   <h2> 
export const SideBarTitle = styled.h2`
    text-align: center;
    padding: 50px 0;
    font-family: 'OurFont1', sans-serif; /*   */
    font-size: 30px;
    font-weight: bold;
    color: black;
    background-color:#F4A460;  //F4A460  #ECAB81 #d67935
`;


export const CurrentLocation = styled.p`
    text-align: center;
    padding: 20px 0;
    font-size: 15px;
    font-weight: bold;
    color: #333;
    
    color: black;
    background-color:#FFF5EE   //#F5F5DC  #fcfcfc  #fff6eb;
    border-bottom: 6px double #ddd;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);

`;


export const SideBarComponent = styled.div`
  position: fixed;
  z-index: 20;
  background-color:#FFF5EE; // #fff4eb; ; {/* //  # : #F5F5DC : #FFFDD0   : #D2B48C   : #FAFAD2  : #fcfcfc*/}
  color: #202020;
  top: 0;
  left: 0;
  bottom: 0;
  border-right: 3.5px solid #202020;
  transition: 0.4s ease;
  height: 100%;
  width: ${({ width }) => `${width}px`}; 
  transform: ${({ $xPosition }) => `translatex(${$xPosition}px)`};  
`;
//React    props  custom props  , custom props    Propery   . so,    $ 


/*
position: relative           .   absolute     . 
, SideBarButton relative , Content   relative         -> SideBarButton    .
so, SideBarButton position absolute .   SideBarButton    Content  .  .*/
export const SideBarButton = styled.button`
  position: absolute; 
  z-index: 20;
  border: 3.5px solid #202020;
  border-radius: 40px;
  height: 50px;
  width: 50px;
  //top: 10px;
  top: 50%; //   
  //left: 360px;
  left: ${({ width }) => `${width}px`}; //   + 10    . left        .
  transition: 0.8s ease;
  cursor: pointer;
  //background-color: #f4a460; /*   */

 &:hover {
  transform: scale(0.9) rotateY(20deg); // rotateY  
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.4); // box-shadow   */
  transition: 0.5s ease; //   
  
}
`;

export const ButtonImg = styled.img`
  width: 100%;
  height: 100%;
  pointer-events: none;
`;

//    
export const Content = styled.div`
  position: relative;
  width: 100%;
  height: calc(100% - 180px); //  .  -60px, -180px    ->   .        
  padding: 20px 20px 20px 20px; //top, right, bottom, left
  //padding: 20px; // 20px.   .
  overflow-y: auto; /**/
  box-sizing: border-box; /*padding    */
`;

export const ListItem = styled.li`
  list-style: none;
  margin-bottom: 20px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;

  transition: 0.2s ease; //hover   transition

  &:hover {
    transform: translateY(-10px); // hover    
    box-shadow: 0 8px 8px rgba(0, 0, 0, 0.55); // hover   
  }
`;

export const ListItemImg = styled.img`
  width: 60px;
  height: 60px;
  //border-radius: 50%;
  margin-right: 15px;
  object-fit: cover;
`;

export const ListItemInfo = styled.div`
  display: flex;
  flex-direction: column;
`;

export const ListItemTitle = styled.h5`
  margin: 0 0 5px 0; /*    */
  font-size: 18px;
  font-weight: bold;
  color: #333;
`;

export const ListItemAddress = styled.span`
  margin-top: 1px;
  font-size: 14px;
  color: #666;
`;

export const ListItemDistance = styled.span`
  margin-top: 5px;
  font-size: 14px;
  color: #777;
`;