import React, { useEffect, useRef } from 'react';
import { RoadViewContainer, CloseButton } from "../../styles/BakeryMap/RoadViewStyles";
import ReactDOMServer from 'react-dom/server';
import RoadViewOverlayContent from "./RoadViewOverlayContent";

const { kakao } = window;


const RoadView = ({ position, onClose, place }) => {
  const roadviewRef = useRef(null); // Roadview  DOM    useRef 

  useEffect(() => {
    if (roadviewRef.current) {  // roadviewRef  DOM    
      const roadview = new kakao.maps.Roadview(roadviewRef.current); // Roadview  
      const roadviewClient = new kakao.maps.RoadviewClient(); // //  ID   helper

      //      panoId   
      roadviewClient.getNearestPanoId(
        new kakao.maps.LatLng(position.lat, position.lng),
        50, //  50     ID 
        (panoId) => {
          if (panoId) {
            roadview.setPanoId(panoId, new kakao.maps.LatLng(position.lat, position.lng)); //   ID   
          } else {
            console.error('    .');
          }
        }
      );

      kakao.maps.event.addListener(roadview, 'init', function () {
        const content = ReactDOMServer.renderToString(
          <RoadViewOverlayContent place={place} />
        );

        //   
        const rvCustomOverlay = new kakao.maps.CustomOverlay({
          position: new kakao.maps.LatLng(position.lat, position.lng),
          content: content,
          xAnchor: 0.5, //   x . 1   . default=0.5
          yAnchor: 0.5 //   y . 1   . default=0.5
        });

        rvCustomOverlay.setMap(roadview);

        const projection = roadview.getProjection(); // viewpoint()    projection 

        //  position altitude  viewpoint() .
        const viewpoint = projection.viewpointFromCoords(rvCustomOverlay.getPosition(), rvCustomOverlay.getAltitude());

        roadview.setViewpoint(viewpoint); //        
      });
    }
  }, [position, place]); //    useEffect  

  return (
    <RoadViewContainer>
      <CloseButton onClick={onClose}></CloseButton>
      <div ref={roadviewRef} style={{ width: '100%', height: '100%' }}></div>
    </RoadViewContainer>
  );
};

export default RoadView;