import React, { useState, useEffect } from 'react';
import { ReactTyped } from "react-typed";
import { ClickerGameBackground, ClickerGameMessage, NextButton } from '../styles/ClickerGameMainStyles';
import Background from '../assets/images/ClickerGameImages/ClickerGameTutorialBGI6.png';
import Image1 from '../assets/images/ClickerGameImages/ClickerGameChapterIconColor.png';
import Image2 from '../assets/images/ClickerGameImages/ClickerGameChapterIconBlack.png';

const TutorialStep4 = ({ onNext }) => {
    const [imagesToShow, setImagesToShow] = useState([]); //    

    useEffect(() => {

        // 2.5     
        const timer1 = setTimeout(() => {
            setImagesToShow((prev) => [...prev, Image1]); //   Image1 
        }, 2500);

        // 3.5     
        const timer2 = setTimeout(() => {
            setImagesToShow((prev) => [...prev, Image2]); //   Image2 
        }, 3500);

        //     
        return () => {
            clearTimeout(timer1);
            clearTimeout(timer2);
        };
    }, []);

    return (
        <ClickerGameBackground bgImage={Background}>
            <div
                style={{
                    position: "absolute",
                    top: "40%", //     
                    left: "50%", //     
                    transform: "translate(-50%, -50%)", //   
                    display: "flex", //  
                    gap: "20px", //   
                    zIndex: 10,
                }}
            >
                {imagesToShow.map((src, index) => (
                    <img
                        key={index}
                        src={src}
                        alt={`Tutorial ${index + 1}`}
                        style={{
                            width: "180px", //  
                            height: "auto", //  
                        }}
                    />
                ))}
            </div>

            <ClickerGameMessage>
                <ReactTyped
                    strings={[
                        "       ,",
                        "' '   ' '     ."
                    ]}
                    typeSpeed={40}
                    loop={false}
                />
            </ClickerGameMessage>
            <NextButton onClick={onNext}></NextButton>
        </ClickerGameBackground>
    );
};

export default TutorialStep4;