import React, { useEffect } from 'react';

//    ,       
const NotFound = () => {

    //body  .
    useEffect(() => {
        document.body.className = 'body-NotFound';
        return () => {
            document.body.className = '';
        };
    }, []);

    return (
        <div
            style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                fontSize: 64,
                position: "absolute",
                width: "100%",
                height: "100%",
            }}
        >
            404: Path Not Found
        </div>
    );

};

export default NotFound;