import styled from 'styled-components';

export const ModalOverlay = styled.div`
  background-color: rgba(0, 0, 0, 0.75); /*   */
  position: fixed; /*   */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex; /*    Flexbox  */
  justify-content: center; /*    */
  align-items: center; /*    */
  z-index: 1000; /*     */
`;

export const ModalContent = styled.div`
  position: relative; /*         */
  padding: 20px;
  border-radius: 10px;
  background-color: #efe3d4; /*   */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /*   */
  max-width: 700px; /*    */
  width: 100%; /*  100% */
  max-height: 680px;
`;

export const ModalSubContent = styled.div`
  max-height: 360px;
  overflow-y: auto;
  padding: 5px 0 5px 10px;

  border-radius: 10px;
  background-color: #ffffff;
  &::-webkit-scrollbar {
    display: none;
  }
`;

export const RecipeTitle = styled.h3`
  font-size: 32px;
  text-align: center;
  font-family: 'OurFont4';
  margin: 0 0 10px 0;
`;
export const RecipeSubTitle = styled.h4`
  font-size: 22px;
  font-family: 'OurFont5';
  margin: 10px 0 13px 3px;
`;

export const RecipeImage = styled.img`
  display: block;
  margin: 5px auto 15px auto;
  width: calc(274px * 1.3);
  height: calc(169px * 1.3);
`;

export const CloseButton = styled.button`
  position: absolute; /*    */
  top: 18px;
  right: 18px;
  background: none; /*   */
  border: none; /*   */
  font-size: 24px; /*   */
  cursor: pointer; /*    */
  color: #333; /*   */

  &:hover {
    color: #ff0000; /*     */
  }
`;

export const RecipeLine = styled.li`
  margin: 0 0 10px 7px;
`;
