import React, { useState, useEffect } from 'react';
import { Header, Nav, NavCenter, Button, ImageButton } from '../styles/PageHeader1Styles';
import { Link } from 'react-router-dom';

function PageHeader1({ initialVisibility = true }) { //  props  
  const [isVisible, setIsVisible] = useState(initialVisibility); //  props 

  //    
  const handleMouseMove = (e) => {
    if (e.clientY < 50) { //    50px    
      setIsVisible(true);
    } else { //      
      setIsVisible(false);
    }
  };

  useEffect(() => {
    //     
    window.addEventListener('mousemove', handleMouseMove);

    //      
    return () => {
      window.removeEventListener('mousemove', handleMouseMove);
    };
  }, []);

  return (
    <Header style={{
      position: 'fixed', //   
      top: isVisible ? '0' : '-80px', // isVisible true  , false   
      width: '100%', //   
      transition: 'top 0.3s ease-in-out', //  
      zIndex: 1000, //    
    }}>

      <Nav> {/*   */}
        <Link to="/">
          <ImageButton> {/*     */}
            <img src={require('../assets/images/homebutton.jpg')} alt=" " />
          </ImageButton>
        </Link>

        <NavCenter>
          <Link to="/BakeryMap">
            <Button>  </Button>
          </Link>
          <Link to="/ClickerGameMain">
            <Button> </Button>
          </Link>
          <Link to="/BakingClass">
            <Button> </Button>
          </Link>
        </NavCenter>
      </Nav>
    </Header>
  );
}

export default PageHeader1;
