import { useEffect } from 'react'
import { Howl } from 'howler'; //Howler.js:      JavaScript .    (mp3,wav, ogg ...),  API  ->        

//   useBgm  
//fadeoutTime : bgm           . fadeoutTime 0     .
function useBgm(path, volume, fadeoutTime) {
    let bgm; //Howl 

    const bgmPlay = (path) => { //   bgm 
        bgm = new Howl({ src: [path] }); // Howl:  .      
        bgm.volume(volume); //  
        bgm.play(); // bgm 
    }

    const bgmStop = () => bgm.stop(); // bgm 

    useEffect(() => {
        bgmPlay(path); //    ( ) bgm  &.

        // bgm.on('play', ...): Howler.js    . bgm  'play'   ,        . 
        //    play    =>     setTimeout      .

        // bgm.fade(volume, 0, foTime):     Howler.js fade . foTime     .  (volume) -> 0   

        //((bgm.duration() - bgm.seek()) * 1000 - foTime)  fadeout  
        // bgm.duration(): bgm.duration()    
        // bgm.seek(): bgm    ( ms)

        //        time diff   .  timeDiff  .  timeDiff 1000   ms   foTime (  )  =>        .    bgm.fade()      

        //,     ,            bgm.fade()       

        bgm.on('play', () => {

            const foTime = fadeoutTime;

            setTimeout(
                () => bgm.fade(volume, 0, foTime),
                (bgm.duration() - bgm.seek()) * 1000 - foTime);

        });

        return bgmStop; //      bgm .

    }, []);
}

export default useBgm;