import React, { useEffect, useState } from 'react';
import Map from '../components/BakeryMap/Map';
import SideBar from '../components/BakeryMap/SideBar';
import styled from 'styled-components';
import PageHeader1 from '../components/PageHeader1.js';

const DIV = styled.div`
  max-width: 85%;
  margin: 0 auto;
  margin-top: 2.5em; //  
  padding-top: 80px; /* PageHeader     */
  padding: 30px;
  background-color: #ffffff;
  border-radius: 45px;
`;

// h1  
const StyledH1 = styled.h1`
  color: #241308; /*   */
  text-align: center; /*   */
  margin-top: 30px;
  margin-bottom: 30px;
  font-family: 'OurFont1', sans-serif; /*   */
  font-size: 30px;
`;

// BakeryMap    BakeryMap    , 
function BakeryMap() {
    const [places, setPlaces] = useState([]); //  BakeryMap . Map   Map         .

    const [currentAddress, setCurrentAddress] = useState(''); //   
    const [selectedItem, setSelectedItem] = useState(null); //   

    useEffect(() => {
        document.body.className = 'body-Map';
        return () => {
            document.body.className = '';
        };
    }, []);

    return (

        <DIV> {/* DIV    .body  */}
            <PageHeader1 />
            <SideBar width={450} places={places} currentAddress={currentAddress} setSelectedItem={setSelectedItem} />
            {/* StyledH1     */}
            <StyledH1>  !</StyledH1>
            <Map setPlaces={setPlaces} setCurrentAddress={setCurrentAddress} selectedItem={selectedItem} />
        </DIV>
    );
}

export default BakeryMap;