import React from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import MainScreen from '../pages/MainScreen';
import BakeryMap from '../pages/BakeryMap';
import BakingClass from '../BakingClass';
import ClickerGame from '../pages/ClickerGameMain';
import ClickerGameTutorial from '../pages/ClickerGameTutorial';
import ClickerGameChapter from '../pages/ClickerGameManageChapterPages';
import NotFound from "../pages/NotFound";
import { GlobalStyle } from '../styles/GlobalStyle';
import { ChapterManageProvider } from '../pages/ClickerGameMain'; // Context 


//React Router SPA(Single Page Application)       .
//,     url      SPA   .

// Router:   ,       
function Router() {


    return (
        <BrowserRouter basename={process.env.PUBLIC_URL}>
            {/* BrowserRouter        . a   url               .       (Routes, Route ) .  History API  URL  URL   .*/}

            {/* Routes   Route   .
             Link URL   Routes   Route   URL        App.js <div className='div-App'>  */}

            {/* path  URL  , component      .
            "/"   .*/}

            {/*   workflow=> BrowserRouter  URL   -> Routes  url  Route   Route element    */}
            {/*<BodyStyleUpdater /> */}
            <GlobalStyle />
            <ChapterManageProvider>
                <Routes>

                    <Route path="/" element={<MainScreen />} />
                    <Route path="/BakeryMap" element={<BakeryMap />} />
                    <Route path="/ClickerGameMain" element={<ClickerGame />} />
                    <Route path="/ClickerGameTutorial/:id" element={<ClickerGameTutorial />} />
                    <Route path="/ClickerGameChapter/:chapter/:id" element={<ClickerGameChapter />} />
                    <Route path="/BakingClass" element={<BakingClass />} />
                    {/*<Route path="/Recipe" component={Recipe} />} */}
                    <Route path="*" element={<NotFound />} />
                    {/*      */}

                </Routes>
            </ChapterManageProvider>
        </BrowserRouter>
    );
}

export default Router;
